:root {
  --antique-white: #faebd7;
  --chestnut: #a44a3f;
  --slate-gray: #738290;
  --gunmetal: #272d2d;
  --moonstone: #50b2c0;
}

body {
  background-color: var(--antique-white);
  color: var(--gunmetal);
}

h1, h2, h3, h4, h5, h6 {
  font-family: Montserrat, sans-serif;
  font-weight: 800;
}

p {
  font-family: Special Elite, serif;
}

.main_app {
  grid-template: "Left-Margin-Top Header Right-Margin-Top" min-content
                 "Sketch Sketch Sketch" 1fr
                 "Sketch Sketch Sketch" 1fr
                 "Sketch Sketch Sketch" 1fr
                 "Left-Margin Content Right-Margin" 1fr
                 / 1fr minmax(50%, 85ch) 1fr;
  gap: 0;
  display: grid;
}

.Right-Margin {
  grid-area: Right-Margin;
}

.Left-Margin {
  grid-area: Left-Margin;
}

.Sketch {
  background: #faebd7;
  flex-flow: row;
  grid-area: Sketch;
  justify-content: center;
  align-items: flex-start;
  display: flex;
  overflow: hidden;
}

.Sketch > canvas {
  box-shadow: 0 10px 20px #00000030, 0 6px 6px #0000003b;
}

.Content {
  grid-area: Content;
}

.Header {
  background-color: var(--moonstone);
  flex-flow: row;
  grid-area: Header;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.Header > h1 {
  font-family: Montserrat Subrayada, sans-serif;
}

.Header > nav > .dropdown {
  display: inline-block;
  position: relative;
}

.Header > nav > .dropdown > button {
  background-color: var(--gunmetal);
  color: var(--antique-white);
  cursor: pointer;
  border: none;
  padding: 16px;
  font-family: Montserrat, sans-serif;
  font-weight: 600;
}

.Header > nav > .dropdown > div {
  background-color: var(--antique-white);
  z-index: 1;
  display: none;
  position: absolute;
  right: 0;
  box-shadow: 0 8px 16px #0003;
}

.Header > nav > .dropdown > div > a {
  color: var(--gunmetal);
  padding: 12px 16px;
  font-family: Special Elite , serif;
  text-decoration: none;
  display: block;
}

.Header > nav > .dropdown > div > a:hover {
  background-color: var(--moonstone);
}

.Header > nav > .dropdown:hover > div {
  display: block;
}

.Left-Margin-Top {
  background-color: var(--moonstone);
  grid-area: Left-Margin-Top;
}

.Right-Margin-Top {
  background-color: var(--moonstone);
  grid-area: Right-Margin-Top;
}

